.rate_btn_container{
margin: 0;
padding: 0; 
width: 270px;
height: 70px;
}

.rate_btn {
    padding:0;
    margin:0;
    display: inline-flex;
    flex-direction: row;
    align-items: flex-end;
    box-sizing: border-box;
    width: 120px;
    height: 24px;
}

.rate_btn:last-child{
    margin-left: 20px;
}

.rate_icon {
    font-size: 24px;
    line-height: 20px;
    text-align: center;
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
   
    
}

